import React, { useState } from "react";
import { connect } from "react-redux";
import { GameData } from "../../../Redux/GameDataReducer/GameModel";
import { PlayerModel } from "../../../Redux/GameDataReducer/PlayerReducer/PlayerModel";
import { ImportPlayerData } from "../../../Redux/GameDataReducer/Actions";

const NameDialogInternal: React.FC<{ ImportPlayerData: typeof ImportPlayerData, close: () => void; players: { name: string, id: string }[] }> = (props) => {
    const [data, setData] = useState<string>("");

    const [message, setMessage] = useState<string>();
    const [state, setState] = useState<"empty" | "loaded">("empty");



    function prepareImport() {
        const indexOfParathesis = data.indexOf("(");
        if (indexOfParathesis !== -1) {
            setMessage("Data blocked");
            return;
        }

        let playerModel: PlayerModel;

        try {
            playerModel = JSON.parse(data);
        }
        catch (error) {
            setMessage("Data import failed, something is wrong with the data");
            return;
        }

        if (playerModel.name === undefined || playerModel.id === undefined) {
            setMessage("Data import failed, missing data");
            return;
        }

        const player = props.players.find(p => p.id === playerModel.id);
        if (player === undefined) {
            setMessage(`Import new player with name '${playerModel.name}'?`);
        }
        if (player !== undefined) {
            if (player.name !== playerModel.name) {
                setMessage(`Update player '${player.name}', which will be renamed to '${playerModel.name}'?`);
            } else {
                setMessage(`Update player '${player.name}'?`);
            }
        }

        setState("loaded");
    };

    function importData() {
        props.ImportPlayerData(JSON.parse(data));
        props.close();
    }

    return <>
        <p>Only import data from sources you trust!</p>
        <input onChange={(value) => setData(value.target.value)} value={data} ></input>
        <p>{message}</p>
        <div style={{ display: "flex", flexDirection: "row" }}>
            {state === "empty" && <button onClick={prepareImport}>Preload</button>}
            {state === "loaded" && <button onClick={importData}>Confirm</button>}
            <button onClick={() => props.close()}>Cancel</button>
        </div>
    </>;
};

const mapStateToProps = (state: GameData) => {
    return {
        players: state.players.map(p => ({ name: p.name, id: p.id }))
    };
};

export const ImportDialog = connect(
    mapStateToProps,
    { ImportPlayerData }
)(NameDialogInternal);