import { MovingOutputChannel } from "../../Components/DragItemTypes";
import { ScreenType } from "./GameModel";
import { PlayerModel } from "./PlayerReducer/PlayerModel";

interface SwitchScreen {
    type: "SwitchScreen";
    screen: ScreenType;
}

export function SwitchScreen(screen: ScreenType): ActionTypes {
    return {
        type: "SwitchScreen",
        screen
    };
}

interface ChangePlayerName {
    type: "ChangePlayerName";
    id: string;
    name: string;
}

export function ChangePlayerName(name: string, id: string): ActionTypes {
    return {
        type: "ChangePlayerName",
        id,
        name
    };
}

interface ImportPlayerData {
    type: "ImportPlayerData";
    data: PlayerModel;
}

export function ImportPlayerData(data: PlayerModel): ActionTypes {
    return {
        type: "ImportPlayerData",
        data
    };
}

interface SwitchPlayer {
    type: "SwitchPlayer";
    id: string;
}

export function SwitchPlayer(id: string): ActionTypes {
    return {
        type: "SwitchPlayer",
        id
    };
}

interface DeletePlayer {
    type: "DeletePlayer";
    id: string;
}

export function DeletePlayer(id: string): ActionTypes {
    return {
        type: "DeletePlayer",
        id
    };
}


interface AddPlayer {
    type: "AddPlayer";
    name: string;
}

export function AddPlayer(name: string): ActionTypes {
    return {
        type: "AddPlayer",
        name
    };
}


interface UpdateStepTime {
    type: "UpdateStepTime";
    stepTime: number;
}

export function UpdateStepTime(stepTime: number): ActionTypes {
    return {
        type: "UpdateStepTime",
        stepTime
    };
}

interface UpdateShowAnimations {
    type: "UpdateShowAnimations";
    show: boolean;
}

export function UpdateShowAnimations(show: boolean): ActionTypes {
    return {
        type: "UpdateShowAnimations",
        show
    };
}
interface SetMovingOutput {
    type: "SetMovingOutput";
    outputChannel: MovingOutputChannel | undefined;
}

export function SetMovingOutput(outputChannel: MovingOutputChannel | undefined): ActionTypes {
    return {
        type: "SetMovingOutput",
        outputChannel
    };
}
export type ActionTypes = UpdateStepTime | UpdateShowAnimations | SwitchScreen
| ChangePlayerName | SetMovingOutput | ImportPlayerData | SwitchPlayer | DeletePlayer | AddPlayer;