import { GameData } from "../Redux/GameDataReducer/GameModel";
import { PlayerModel } from "../Redux/GameDataReducer/PlayerReducer/PlayerModel";
import { CustomMachineModel } from "../Redux/GameDataReducer/PlayerReducer/GridSetup/CustomMachineModel";

export function GetActivePlayer(state: GameData): PlayerModel|undefined {
    return state.players.find((p) => p.id === state.activePlayerId);
}

export function GetActiveMachine(state: GameData): CustomMachineModel | undefined {
    const player = GetActivePlayer(state);

    const newActiveMachine = player?.machines.find((m) => m.Id === player.activeMachineId);

    return newActiveMachine;
}

export function GetCellSize(state: GameData): number{
    return GetActivePlayer(state)!.cellSize;    
}