import { digits, lowerLetters, specials } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const KeepNumbers: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "11", "12"],
    LevelDescription: "The machine should keep all numbers in the input string, and remove all other characters",
    MachineDescription: "Keeps all numbers in the input string, and removes all other characters",

    Name: "KeepNumbers",
    Id: "6",
    Style: {
        Color: "PeachPuff",
        BorderColor: "DarkOrchid",
    },
    InputChannels: [],
    OutputChannels: [
        { Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Down, Status: "Exists" },
    ],
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "42" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "fortytwo" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "1 and 1 is" }],
            showing: true
        }
    ],
    Cell: {
        Row: 3,
        Column: 3
    },
    GetRandomTestInputs: () => GetDefaultTestInput(digits + lowerLetters + specials),
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let content = "";
        for (const char of input.Content) {
            if (digits.indexOf(char) !== -1) {
                content += char;
            }
        }
        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: content
        })
    }

    return outputs;
}