import { lowerLetters } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const ReverseLimited: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "11", "12"],
    LevelDescription: "The machine should reverse the characters from the string. Input are only lowercase letters. With limited number of machines at your disposal, this is VERY HARD",
    MachineDescription: "Reverses the characters from the string. Input can only consist of lowercase letters",

    Name: "ReverseLimited",
    Id: "10",
    Style: {
        Color: "MediumSlateBlue",
        BorderColor: "Black",
    },
    InputChannels: [],
    OutputChannels: [
        { Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" },
    ],
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "a" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "esrever" }],
            showing: true
        }
    ],
    Cell: {
        Row: 1,
        Column: 9
    },
    GetRandomTestInputs: () => GetDefaultTestInput(lowerLetters),
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let text = "";

        for (const char of input.Content) {
            if (lowerLetters.indexOf(char) === -1) {
                return [];
            }
        }

        for (let i = input.Content.length - 1; i >= 0; i--) {
            text += input.Content[i];
        }
        
        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: text
        })
    }

    return outputs;
}