import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Computer } from "../../Computing/Computer";
import { GetActiveMachine } from "../../Helper/StoreHelper";
import { CheckTest } from "../../Helper/TestHelper";
import { LevelModel } from "../../Levels/LevelModel";
import { ExecutedTest } from "../../Models/ExecutedTest";
import { SwitchScreen } from "../../Redux/GameDataReducer/Actions"
import { SetCustomTest } from "../../Redux/GameDataReducer/Level/Actions"
import { SetLevelComplete } from "../../Redux/GameDataReducer/PlayerReducer/Actions"
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { CustomMachineModel } from "../../Redux/GameDataReducer/PlayerReducer/GridSetup/CustomMachineModel";
import { AppState } from "../../Redux/Store";
import { TestDisplay } from "../LevelDisplay/TestDisplay/TestDisplay";
import { LevelStore } from "../../Levels/LevelStore";
import { GetOpaqueColor } from "../../Helper/ColorHelper";

const CheckLevelInternal: React.FC<{
    Level: LevelModel,
    close: () => void,
    SetLevelComplete: typeof SetLevelComplete;
    SwitchScreen: typeof SwitchScreen;
    SetCustomTest: typeof SetCustomTest
}> = (props) => {
    const [state, setState] = useState<"Checking" | "Passed" | "Failed">("Checking");
    const [testsSucceeded, setTestsSucceeded] = useState(0);
    const [failedTest, setFailedTest] = useState<undefined | (ExecutedTest & {showingTest: boolean})>(undefined);

    useEffect(() => {
        const activeMachine = GetActiveMachine(AppState());

        if (activeMachine !== undefined) {

            RunTests(activeMachine, props.Level, setTestsSucceeded).then((failingTest) => {
                if (failingTest === undefined) {
                    props.SetLevelComplete(props.Level.Id);
                    setState("Passed");
                } else {
                    setFailedTest(failingTest);
                    setState("Failed");
                }
            }).catch(() => {
                props.close();
            });
        }
    }, [setTestsSucceeded, props])
    return <div style={{ width: 266 }}>
        {state === "Checking" && <>
            Checking tests<br />
            {testsSucceeded}/{props.Level.Tests.length + 10} tests passed
        </>
        }
        {state === "Passed" && <>
            All {props.Level.Tests.length + 10} tests have been passed<br />
            <b style={{ color: "green" }}>Level completed!</b><br />
            <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between", marginTop: 10 }} >
                <button onClick={props.close} title="Continue editing this machine">Back</button>
                <button onClick={() => props.SwitchScreen("levelSelect")} title="Go to the level select screen">Select next level</button>
            </div>

        </>}
        {state === "Failed" && <>
            The following test failed:
            <div style={{ backgroundColor: GetOpaqueColor("white", 0.7), padding: 3 }}>
                <TestDisplay CanPlay={false} Test={{ ...failedTest!, id: 0, editable: false, showing: true }} MultipleInputsEditable={false} IsSandBoxLevel={false} />
            </div>
            <div style={{ display: "flex", flexDirection: "row-reverse", justifyContent: "space-between", marginTop: 10 }} >
                <button onClick={props.close} title="Close this screen">Close</button>
                {!failedTest!.showingTest && <button onClick={() => props.SetCustomTest(failedTest!.inputs)} title="Send input to the custom test">Examine</button>}
            </div>
        </>}
    </div>
}

async function RunTests(activeMachine: CustomMachineModel, level: LevelModel, setTestsSucceeded: (test: number) => void): Promise<undefined | (ExecutedTest & { showingTest: boolean })> {
    const computer = new Computer(activeMachine.Setup, true);

    for (let i = 0; i < level.Tests.length + 10; i++) {

        const input = i < level.Tests.length ? level.Tests[i].inputs : level.GetRandomTestInputs();
        const output = await computer.RunInputs(input);
        const expected = level.GetTestExpectedOutputs(input);
        if (CheckTest(expected, output)) {
            setTestsSucceeded(i);
        } else {
            return Promise.resolve({ inputs: input, expectedOutputs: expected, actualOutputs: output, showingTest: i < level.Tests.length && level.Tests[i].showing })
        }
    }

    return Promise.resolve(undefined);
}

const mapStateToProps = (state: GameData) => {

    const Level = LevelStore.GetStore().GetLevel(state.activeLevel!.id)


    return {
        Level
    };
};

export const CheckLevel = connect(
    mapStateToProps,
    { SetLevelComplete, SwitchScreen, SetCustomTest }
)(CheckLevelInternal);