import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const RotateBackwards: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "11", "12"],
    LevelDescription: "The machine should put the last character in front of the string",
    MachineDescription: "Rotates the last character to the front of the string",

    Name: "RotateBackwards",
    Id: "11",
    Style: {
        Color: "Blue",
        BorderColor: "Cyan",
    },
    InputChannels: [],
    OutputChannels: [
        { Channel: ChannelColorEnum.Red, Direction: DirectionEnum.Right, Status: "Exists" },
        { Channel: ChannelColorEnum.Blue, Direction: DirectionEnum.Up, Status: "Exists" },
        { Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Down, Status: "Exists" },
    ],
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "otater" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "a" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "heat proofC" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "ello world!H" }],
            showing: true
        }
    ],
    Cell: {
        Row: 2,
        Column: 5
    },
    GetRandomTestInputs: GetDefaultTestInput,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)){
        let content = input.Content.length > 1 ? input.Content[input.Content.length -1] + input.Content.slice(0, input.Content.length - 1): input.Content;
        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: content
        })
    }

    return outputs;    
}