import { start } from "repl";
import { digits, lowerLetters, upperLetters } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const Decrement: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "11", "12"],
    LevelDescription: "The machine should decrement the leading character, turning 0 into 9, a/A into z/Z",
    MachineDescription: "Decrements the leading character",

    Name: "Decrement",
    Id: "20",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "bbc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "0321" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "@bc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "Iello world" }],
            showing: true
        }
    ],
    Style: {
        Color: "MediumVioletRed",
        BorderColor: "Orange",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" },
    ],
    Cell: {
        Row: 2,
        Column: 4
    },
    GetRandomTestInputs: GetDefaultTestInput,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let outputContent = "";
        if (input.Content.length > 0) {
            const startChar = input.Content[0];
            let result = startChar;
            let done = false;

            [done, result] = DecrementChar(startChar, digits);
            if (!done) {
                [done, result] = DecrementChar(startChar, lowerLetters);
            }
            if (!done) {
                [done, result] = DecrementChar(startChar, upperLetters);
            }           

            outputContent = result + input.Content.substring(1);
        }

        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: outputContent
        })
    }

    return outputs;
}

function DecrementChar(char: string, charSet: string): [boolean, string] {
    const index = charSet.indexOf(char);
    if (index === -1) {
        return [false, char];
    }

    if (index > 0) {
        return [true, charSet[index - 1]];
    } else {
        return [true, charSet[charSet.length-1]];
    }
}
