import { OrderedMessageModel } from "../Models/OrderedMessageModel";

export function CheckTest(expecteds: OrderedMessageModel[], actuals: OrderedMessageModel[]): boolean {
    if (expecteds.length !== actuals.length) {
        return false;
    }
    for (const expected of expecteds) {
        const actual = actuals.filter(a => a.Order === expected.Order && a.Channel === expected.Channel && a.Content === expected.Content);
        if (actual.length !== 1) {
            return false;
        }
    }
    return true;
}