import { digits, lowerLetters, upperLetters } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const IncrementAll: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "12"],
    LevelDescription: "The machine should increment all characters",
    MachineDescription: "Increments all characters",

    Name: "IncrementAll",
    Id: "21",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "987" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "1+1=3" }],
            showing: true
        }
    ],
    Style: {
        Color: "Ivory",
        BorderColor: "MediumVioletRed",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" },
    ],
    Cell: {
        Row: 2,
        Column: 2
    },
    GetRandomTestInputs: GetDefaultTestInput,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let content = "";
        for (const char of input.Content) {
            content += IncrementChar(char);
        }
        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: content
        })
    }

    return outputs;
}

export function IncrementChar(char: string): string {
    let result = char;
    let done = false;

    [done, result] = Increment(char, digits);
    if (!done) {
        [done, result] = Increment(char, lowerLetters);
    }
    if (!done) {
        [done, result] = Increment(char, upperLetters);
    }

    return result;
}

function Increment(char: string, charSet: string): [boolean, string] {
    const index = charSet.indexOf(char);
    if (index === -1) {
        return [false, char];
    }

    if (index < charSet.length - 1) {
        return [true, charSet[index + 1]];
    } else {
        return [true, charSet[0]];
    }
}