import React, { useState } from "react";
import { connect } from "react-redux";
import { SwitchScreen } from "../../Redux/GameDataReducer/Actions";
import { LoadLevel } from "../../Redux/GameDataReducer/Level/Actions";
import { Props } from "./Props";
import { GetActivePlayer } from "../../Helper/StoreHelper";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { PlayersDialog } from "./Dialog/PlayersDialog";
import { LevelState } from "../../Redux/GameDataReducer/PlayerReducer/LevelData";
import { LevelStore } from "../../Levels/LevelStore";
import { LevelModel } from "../../Levels/LevelModel";
import { MenuBox } from "./MenuBox";

const StartMenuInternal: React.FC<Props> = (props) => {
    const [dialog, setdialog] = useState<"none" | "editPlayers" | "importData">("none");

    return (
        <div style={{ height: "100%", width: "100%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>
            <h1>Contexion</h1>
            {dialog === "none" && <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
                <MenuBox>
                    {props.continueLevel !== undefined && <button className="startMenu" onClick={() => props.LoadLevel(props.continueLevel!.Id)}>Continue with {props.continueLevel.Name}</button>}
                    {props.playerName !== undefined && <>
                        <button className="startMenu" onClick={() => props.SwitchScreen("levelSelect")}>Select level</button>
                        <button className="startMenu" onClick={() => props.LoadLevel("12")}>Sandbox</button>
                    </>}
                    <button className="startMenu" onClick={() => setdialog("editPlayers")}>Manage players</button>
                </MenuBox>
                {props.playerName === undefined ? <h4>No player profile, make one to play!</h4> : <h4> Playing as {props.playerName}</h4>}
            </div>}
            {dialog === "editPlayers" &&
                <MenuBox>
                    <PlayersDialog close={() => {
                        setdialog("none");
                    }}></PlayersDialog>
                </MenuBox>}
        </div>
    );
};

const mapStateToProps = (state: GameData) => {
    const player = GetActivePlayer(state);
    let continueLevel: LevelModel | undefined = undefined;
    if (player?.activeLevel !== undefined) {
        const data = player.levelData.find((ld) => ld.Id === player.activeLevel);
        if (data?.State === LevelState.InProgress || data?.State === LevelState.NonValidMachineUsed) {
            continueLevel = LevelStore.GetStore().GetLevel(player.activeLevel);
        }
    }

    return {
        playerName: player?.name,
        continueLevel
    };
};

export const StartMenu = connect(
    mapStateToProps,
    { SwitchScreen, LoadLevel }
)(StartMenuInternal);
