import React, { useState } from "react";
import { GetLighterColor, GetRandomColor } from "../../Helper/ColorHelper";

export const MenuBox: React.FC<unknown> = (props) => {
    const [backgroundColor] = useState(GetLighterColor(GetRandomColor(), 0.3));
    const [borderColor] = useState(GetRandomColor());

    return <div style={{ borderColor, backgroundColor, borderWidth: 20, borderStyle: "solid", padding: 30, display: "flex", flexDirection: "column" }}>
        {props.children}
    </div>;
};