export function GetRandomString(chars: string = allChars, minLength: number = 4, maxLength: number = 12): string{
    const length = Math.round(Math.random() * (maxLength - minLength)) + minLength;
    let text = "";
    const charsLength = chars.length;
    for(let i = 0; i < length; i++){
        text += chars[Math.round(Math.random()*(charsLength - 1))];
    }

    return text;
}

export const lowerLetters = "abcdefghijklmnopqrstuvwxyz";
export const upperLetters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
export const digits = "0123456789";
export const specials = "!@#$%^&_-+= []\\|:;?.,/'";
export const allChars = lowerLetters + upperLetters + digits + specials;