import { specials } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";
import { IncrementChar } from "./IncrementAll";

export const IncrementIncreasing: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "12"],
    LevelDescription: "The machine should increment the first character 0 times, the second character once, the third twice etcetera. Characters that cannot be incremented do not count",
    MachineDescription: "Increments the first character 0 times, the second character once, the third twice, etcetera. Characters that cannot be incremented do not count",

    Name: "IncrementIncreasing",
    Id: "22",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "aaa" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "5432109" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "1-A2@q" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "hdjik rikdu!" }],
            showing: true
        }
    ],
    Style: {
        Color: "Pink",
        BorderColor: "Purple",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Up, Status: "Exists" },
    ],
    Cell: {
        Row: 1,
        Column: 5
    },
    GetRandomTestInputs: GetDefaultTestInput,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let content = "";
        let times = 0;
        for (let char of input.Content) {
            if (specials.indexOf(char) !== -1){
                content += char;
                continue;
            }
            for (let i = 0; i < times; i++) {
                char = IncrementChar(char)
            }

            times++;
            content += char;
        }
        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: content
        })
    }

    return outputs;
}