import React, { useState } from "react";

export const NameDialog: React.FC<{name: string, close: (name: string) => void}> = (props) => {
    const [name, setName] = useState(props.name);

    function handleKeyUp(event: React.KeyboardEvent<HTMLInputElement>): void
    {
        switch (event.key) {
            case "Enter":
                props.close(name);
                break;        
            default:
                break;
        }
    }
    return <div style={{width: 200, margin: 10}}>
        <input onChange={(value) => setName(value.target.value)} onKeyUp={handleKeyUp} value={name} ></input>
        <button onClick={() => props.close(name)}>OK</button>
        <button onClick={() => props.close("")}>Cancel</button>

    </div>;
};