import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetTestInputsWithRepeatsInputs } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const RemoveUntilRepeat: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "9"],
    LevelDescription: "The machine should delete all characters before the first repeated character",
    MachineDescription: "Deletes all character before the first repeated character",

    Name: "RemoveUntilRepeat",
    Id: "19",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abbc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abca" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "22" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "1" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "hello world" }],
            showing: true
        }
    ],
    Style: {
        Color: "LawnGreen",
        BorderColor: "DarkGrey",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Down, Status: "Exists" },
    ],
    Cell: {
        Row: 2,
        Column: 0
    },
    GetRandomTestInputs: GetTestInputsWithRepeatsInputs,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)){
        let text = "";
        let content = input.Content;
        while(content.length > 1){
            if (content[0] === content[1]){
                text = content;
                break;
            } else {
                content = content.slice(1);
            }
        }
        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: text
        })
    }

    return outputs;    
}