import React, { useState } from "react";
import { connect } from "react-redux";
import { GameData } from "../../../Redux/GameDataReducer/GameModel";
import { ChangePlayerName, DeletePlayer, AddPlayer, SwitchPlayer } from "../../../Redux/GameDataReducer/Actions"
import { NameDialog } from "./Player/NameDialog";
import { AppState } from "../../../Redux/Store";
import { ImportDialog } from "./ImportDialog";

const PlayersDialogInternal: React.FC<{
    close: () => void,
    ChangePlayerName: typeof ChangePlayerName,
    AddPlayer: typeof AddPlayer,
    SwitchPlayer: typeof SwitchPlayer,
    DeletePlayer: typeof DeletePlayer,
    players: { name: string, id: string, active: boolean }[]
}> = (props) => {

    const [dialog, setDialog] = useState<"none" | "deletePlayer" | "exportPlayer" | "importPlayer" | "addPlayer">("none");
    const [selectedPlayer, setSelectedPlayer] = useState<string>("");
    const [playerIdToBeDeleted, setPlayerIdToBeDeleted] = useState<string>("");
    const [playerData, setPlayerData] = useState<string>("");
    const [message, setMessage] = useState<string>("");


    function SetDeleteDialog(name: string, id: string) {
        setDialog("deletePlayer");
        setSelectedPlayer(name);
        setPlayerIdToBeDeleted(id);
    }

    function SetExportDialog(name: string, id: string) {
        const player = AppState().players.find(p => p.id === id);
        if (player !== undefined) {
            const data = JSON.stringify(player);
            if (-1 !== data.indexOf("(")) {
                setMessage(`Cannot export data of '${name}', it contains a '('`);
            } else {
                setMessage(`Exporting data of '${name}', copy data below`);
                setPlayerData(data);
            }

            setDialog("exportPlayer");
        }
    }

    function deletePlayer(id: string) {
        setDialog("none");
        props.DeletePlayer(id);
    }

    return <>
        <div style={{ display: "flex", flexDirection: "column", opacity: dialog === "none" ? 1 : 0.5 }}>
            {props.players.map(p =>
                <div style={{ display: "flex", flexDirection: "row", border: p.active ? "4px solid lime" : "4px", padding: p.active ? 5 : 9 }}>
                    <input disabled={dialog !== "none"} onChange={(event) => props.ChangePlayerName(event.target.value, p.id)} value={p.name}></input>
                    <button disabled={p.active || dialog !== "none"} onClick={() => props.SwitchPlayer(p.id)}>Activate</button>
                    <button disabled={dialog !== "none"} onClick={() => SetDeleteDialog(p.name, p.id)}>Delete</button>
                    <button disabled={dialog !== "none"} onClick={() => SetExportDialog(p.name, p.id)}>Export</button>
                </div>)}
            {dialog === "none" && <div style={{ padding: 9, display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                <div>
                    <button onClick={() => setDialog("addPlayer")}>Add player</button>
                    <button onClick={() => setDialog("importPlayer")}>Import player</button>
                </div>
                <div>
                    <button onClick={props.close}>Done</button>
                </div>
            </div>}
        </div>
        {dialog === "addPlayer" &&
            <NameDialog name="" close={(name) => {
                if (name !== "") {
                    props.AddPlayer(name);
                }
                setDialog("none");
            }}></NameDialog>}
        {dialog === "deletePlayer" &&
            <div style={{ display: "flex", flexDirection: "column" }}>
                Delete '{selectedPlayer}', are you sure?
                <div>
                    <button style={{ margin: 5 }} onClick={() => deletePlayer(playerIdToBeDeleted)}>Delete</button>
                    <button style={{ margin: 5 }} onClick={() => setDialog("none")}>Cancel</button>
                </div>
            </div>
        }
        {dialog === "exportPlayer" &&
            <div style={{ display: "flex", flexDirection: "column" }}>
                {message}
                <input style={{ margin: 5 }} disabled={true} value={playerData}></input>
                <div>
                    <button style={{ margin: 5 }} onClick={() => { navigator.clipboard.writeText(playerData); setDialog("none"); }}>Copy to clipboard</button>
                    <button style={{ margin: 5 }} onClick={() => setDialog("none")}>Done</button>
                </div>
            </div>
        }
        {dialog === "importPlayer" &&
            <ImportDialog close={() => setDialog("none")}></ImportDialog>}
    </>
};

const mapStateToProps = (state: GameData) => {
    return {
        players: state.players.map(p => ({ name: p.name, id: p.id, active: state.activePlayerId === p.id })),
    };
};

export const PlayersDialog = connect(
    mapStateToProps,
    { ChangePlayerName, DeletePlayer, SwitchPlayer, AddPlayer }
)(PlayersDialogInternal);