import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetTestInputsWithRepeatsInputs } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const RemoveIfDuplicate: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "9"],
    LevelDescription: "If the first characters are duplicates, the machine should clear all the duplicates",
    MachineDescription: "If the first characters are duplicates, clears those duplicates",

    Name: "RemoveIfDuplicate",
    Id: "17",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "aabc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "a" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "33333" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "hhhhhhhello world" }],
            showing: true
        }
    ],
    Style: {
        Color: "MediumAquaMarine",
        BorderColor: "DeepSkyBlue",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Blue, Direction: DirectionEnum.Down, Status: "Exists" },
    { Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" }
    ],
    Cell: {
        Row: 1,
        Column: 0
    },
    GetRandomTestInputs: GetTestInputsWithRepeatsInputs,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let content = input.Content;
        if (content.length > 1) {
            const char = input.Content[0];
            let repeated = false;
            while (content.length > 1 && content[1] === char) {
                content = content.slice(1);
                repeated = true;
            }

            if (repeated) {
                content = content.slice(1);
            }
        }

        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: content
        })
    }

    return outputs;
}