import { allChars } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const AppendLength: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "11", "12"],
    LevelDescription: "The machine should append the length of the input string to the string",
    MachineDescription: "Appends the length of the input string to the string",
    Name: "AppendLength",
    Id: "13",
    Style: {
        Color: "Brown",
        BorderColor: "RebeccaPurple",
    },
    InputChannels: [],
    OutputChannels: [
        { Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" },
    ],
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "four" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "aabccd" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "1" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "This is hard" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "This might even be harder" }],
            showing: true
        }
    ],
    Cell: {
        Row: 1,
        Column: 7
    },
    GetRandomTestInputs: () => GetDefaultTestInput(allChars, 0, 30),
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)){
        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: input.Content + input.Content.length.toString()
        })
    }

    return outputs;    
}