import { createStore } from "redux";
import { CustomMachinesActionTypes } from "./GameDataReducer/PlayerReducer/Actions";
import { GridSetupActionTypes } from "./GameDataReducer/PlayerReducer/GridSetup/Actions";
import { MachineInstanceActionTypes } from "./GameDataReducer/PlayerReducer/GridSetup/MachineInstances/Actions";
import { LevelActionTypes } from "./GameDataReducer/Level/Actions";
import { ActionTypes } from "./GameDataReducer/Actions";
import { GameReducer } from "./GameDataReducer/GameReducer";
import { GameData } from "./GameDataReducer/GameModel";
import { GetNewPlayer } from "./GameDataReducer/GameReducer";
import { Draft } from "immer";
import { PlayerModel } from "./GameDataReducer/PlayerReducer/PlayerModel";
import { LevelState } from "./GameDataReducer/PlayerReducer/LevelData";

function rootReducer(state: GameData | undefined, action: CustomMachinesActionTypes | GridSetupActionTypes | MachineInstanceActionTypes | LevelActionTypes | ActionTypes): GameData {
    if (state === undefined) {
        const player = GetNewPlayer("anonymous");
        return {
            players: [player],
            activePlayerId: player.id,
            messages: [],
            screen: "startMenu",
            activeLevel: undefined,
            movingOutput: undefined
        };
    }

    const returnValue = GameReducer(state, action);
    window.localStorage.setItem("Players", JSON.stringify(returnValue.players));
    window.localStorage.setItem("activePlayer", returnValue.activePlayerId);

    return returnValue;
}

function getInitialState(): GameData | undefined {

    // return TestState as GameData;

    const version = Number.parseInt(window.localStorage.getItem("Version") ?? "0", 10);

    switch (version) {
        case 0:
            window.localStorage.removeItem("State");
            window.localStorage.setItem("Version", "1");

            return undefined;
        case 1:
            const players = JSON.parse(window.localStorage.getItem("Players")!);
            for (const player of players){
                // 3 is levelState completed, 1 is levelState inProgress
                for (const level of player.levelData.filter((l: any) => l.State === 3)){
                    level.State = 1;
                }
            }

            window.localStorage.setItem("Players", JSON.stringify(players));
            window.localStorage.setItem("Version", "2");
    }

    const state: Draft<GameData> = {
        players: JSON.parse(window.localStorage.getItem("Players")!),
        activePlayerId: window.localStorage.getItem("activePlayer")!,
        messages: [],
        screen: "startMenu",
        movingOutput: undefined,
    };

    if (state.players === null) {
        return undefined;
    }

    return state;
}

export const AppStore = createStore(rootReducer, getInitialState(), (window as any).__REDUX_DEVTOOLS_EXTENSION__ && (window as any).__REDUX_DEVTOOLS_EXTENSION__());
export const AppState = () => {
    return AppStore.getState();
};