import { stringify } from "querystring";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetTestInputsWithRepeatsInputs } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const MakeTwins: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10"],
    LevelDescription: "The machine should exit the string with each character repeated once",
    MachineDescription: "Makes sure that each character is repeated once",

    Name: "MakeTwins",
    Id: "18",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "aaabcc" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "hello world" }],
            showing: true
        }
    ],
    Style: {
        Color: "AntiqueWhite",
        BorderColor: "GreenYellow",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Down, Status: "Exists" },
    ],
    Cell: {
        Row: 1,
        Column: 2
    },
    GetRandomTestInputs: GetTestInputsWithRepeatsInputs,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let text = "";
        for (const char of input.Content) {
            if (char !== text[text.length - 1]) {
                text += char.repeat(2);
            }
        }

        outputs.push({
            Channel: ChannelColorEnum.Green,
            Order: input.Order,
            Content: text
        })
    }

    return outputs;
}

