import { allChars, GetRandomString } from "../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../Models/ChannelColorsEnum";
import { OrderedMessageModel } from "../Models/OrderedMessageModel";

export function GetDefaultTestInput(chars?: string, minLength?: number, maxLength?: number): OrderedMessageModel[] {
    const inputs: OrderedMessageModel[] = [{
        Order: 0,
        Channel: ChannelColorEnum.Green,
        Content: GetRandomString(chars, minLength, maxLength)
    }];

    return inputs;
}


export function GetMultiChannelInputs(inputMin: number, inputMax: number, green: number, red: number, blue: number, yellow: number): OrderedMessageModel[] {
    const numberOfInputs = Math.round(Math.random() * (inputMax - inputMin)) + inputMin;
    const inputs: OrderedMessageModel[] = [];
    const channelTotal = green + red + blue + yellow;
    for (let i = 0; i < numberOfInputs; i++) {
        const channelNum = Math.random() - 0.499 * channelTotal;
        let channel: ChannelColorEnum = ChannelColorEnum.Green;
        if (channelNum < yellow) {
            channel = ChannelColorEnum.Yellow;
        } else if (channelNum < yellow + blue) {
            channel = ChannelColorEnum.Blue;
        } else if (channelNum < yellow + blue + red) {
            channel = ChannelColorEnum.Blue;
        }

        inputs.push({
            Channel: channel,
            Order: i,
            Content: GetRandomString()
        })
    }

    return inputs;
}

export function GetTestInputsWithRepeatsInputs(): OrderedMessageModel[] {
    const length = Math.round(Math.random() * (6)) + 3;
    let text = "";
    const charsLength = allChars.length;
    for (let i = 0; i < length; i++) {
        const char = allChars[Math.round(Math.random() * (charsLength - 1))];
        let repeat = Math.round(Math.random() * 6);
        if (repeat === 0) {
            repeat++
        }
        text += char.repeat(repeat);
    }

    const inputs: OrderedMessageModel[] = [{
        Order: 0,
        Channel: ChannelColorEnum.Green,
        Content: text
    }];

    return inputs;
}