import { digits, lowerLetters, upperLetters } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const GetType: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "11", "12"],
    LevelDescription: "The machine should exit green if the first character is a number, exit red if it is a letter and exit blue otherwise",
    MachineDescription: "Exits green if the first character is a number, exits red if it is a letter and exits blue otherwise",
    Name: "GetType",
    Id: "16",
    Style: {
        Color: "Orange",
        BorderColor: "LightGoldenRodYellow",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Blue, Direction: DirectionEnum.Down, Status: "Exists" },
    { Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" }
    ],
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "42" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "this starts with a letter" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: ",#@%!" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            showing: true
        }
    ],
    Cell: {
        Row: 2,
        Column: 3
    },
    GetRandomTestInputs: GetDefaultTestInput,
    GetTestExpectedOutputs: GetOutputs
};

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)) {
        let channel: ChannelColorEnum = ChannelColorEnum.Blue;
        if (input.Content.length === 0) {
            channel = ChannelColorEnum.Blue;
        } else {
            const firstChar = input.Content[0];
            if (digits.indexOf(firstChar) !== -1) {
                channel = ChannelColorEnum.Green;
            } else if (lowerLetters.indexOf(firstChar) !== -1) {
                channel = ChannelColorEnum.Red;
            } else if (upperLetters.indexOf(firstChar) !== -1) {
                channel = ChannelColorEnum.Red;
            }
        }
        outputs.push({
            Channel: channel,
            Order: input.Order,
            Content: input.Content
        })
    }

    return outputs;
}