import { digits, GetRandomString } from "../../Helper/RandomStringHelper";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import { GetDefaultTestInput } from "../LevelHelper";
import { LevelModel } from "../LevelModel";

export const PlusOne: LevelModel = {
    AllowedMachines: ["0", "5", "6", "7", "8", "9", "10", "11", "12"],
    LevelDescription: "The machine should increase the leading input number by 1",
    MachineDescription: "Increase the input number by 1",

    Name: "PlusOne",
    Id: "7",
    Style: {
        Color: "Indigo",
        BorderColor: "DarkTurquoise",
    },
    InputChannels: [],
    OutputChannels: [
        { Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" },
    ],
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "4" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "0 and some noise 2" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "42" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "9" }],
            showing: false
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "39" }],
            showing: true
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "23999999 9" }],
            showing: true
        }
    ],
    Cell: {
        Row: 2,
        Column: 6
    },
    GetRandomTestInputs: GetDigitsWithoutLeadingZero,
    GetTestExpectedOutputs: GetOutputs
};

function GetDigitsWithoutLeadingZero(){
    const input = GetRandomString("123456789", 1,1) + GetRandomString(digits + "ab", 0, 5);
    const inputs: OrderedMessageModel[] = [{
        Order: 0,
        Channel: ChannelColorEnum.Green,
        Content: input
    }];

    return inputs;
}

function GetOutputs(inputs: OrderedMessageModel[]): OrderedMessageModel[] {
    const outputs: OrderedMessageModel[] = [];
    for (const input of inputs.filter(i => i.Channel === ChannelColorEnum.Green)){
        let numberPart ="";
        let copied = input.Content;
        while(copied.length > 0){
            const index = digits.indexOf(copied[0]);
            if (index === -1){
                break;
            }

            numberPart += copied[0];
            copied = copied.slice(1);
        }

        if (numberPart.length > 1 && numberPart[0] === "0"){
            return [];
        }
            
        const value = Number.parseInt(numberPart, 10)
        if (!isNaN(value)){

            outputs.push({
                Channel: ChannelColorEnum.Green,
                Order: input.Order,
                Content: (value + 1).toString() + copied
            })
        } else {
            outputs.push({
                Channel: ChannelColorEnum.Green,
                Order: input.Order,
                Content: copied
            })
        }

    }

    return outputs;    
}