import produce, { Draft } from "immer";
import { Constants } from "../../Components/Constants";
import { CreateGuid } from "../../Helper/Guid";
import { GetActiveMachine } from "../../Helper/StoreHelper";
import { CleanInput } from "../../Helper/TextInputHelper";
import { IsDefaultMachine } from "../../MachineStore/IMachine";
import { Actions } from "../Actions";
import { GameData } from "./GameModel";
import { LevelReducer } from "./Level/Reducer";
import { LevelState } from "./PlayerReducer/LevelData";
import { PlayerModel } from "./PlayerReducer/PlayerModel";
import { PlayerReducer } from "./PlayerReducer/PlayerReducer";

export const GameReducer: (state: GameData, action: Actions) => GameData = produce((draft: Draft<GameData>, action: Actions) => {
    switch (action.type) {
        case "SwitchScreen":
            draft.screen = action.screen;
            break;
        case "SetMessagesActionType":
            draft.messages = action.messages;
            break;
        case "SetMovingOutput":
            draft.movingOutput = action.outputChannel;
            break;
        case "ImportPlayerData":
            const existingPlayer = draft.players.findIndex(p => p.id === action.data.id);
            if (existingPlayer !== -1) {
                draft.players[existingPlayer] = action.data;
            } else {
                draft.players.push(action.data);
            }
            break;
        case "SwitchPlayer":
            draft.activePlayerId = action.id;
            break;
        case "DeletePlayer":

            draft.players = draft.players.filter((p) => p.id !== action.id);
            if (draft.activePlayerId === action.id) {
                if (draft.players.length > 0) {
                    draft.activePlayerId = draft.players[0].id;
                } else {
                    draft.activePlayerId = "";
                }
            }
            break;
        case "AddPlayer":
            const newName = CleanInput(action.name)
            const newPlayer = GetNewPlayer(newName);
            draft.players.push(newPlayer);
            break;
        case "ChangePlayerName":
            const player = draft.players.find((p) => p.id === action.id);
            if (player !== undefined) {
                player.name = CleanInput(action.name);
            }
            break;
        default:
            switch (action.type) {
                case "AddNewMachineActionType":
                    draft.messages = [];
                    break;
                case "LoadLevelActionType":
                    draft.screen = "playing";
                    break;
                case "ChannelChangedActionType":
                    if (action.channel.Status === "Exists") {
                        draft.movingOutput = undefined;
                    }
            }

            if (draft.activePlayerId === "") {
                return;
            }

            const activePlayer = draft.players.find((p) => p.id === draft.activePlayerId);
            if (activePlayer !== undefined) {
                PlayerReducer(activePlayer, action);
            }
            break;
    }

    draft.activeLevel = LevelReducer(draft.activeLevel, action);

    switch (action.type) {
        case "ReportOutputActionType":
            CheckLevelCompleted(draft);
            break;
        case "MachineRemovedActionType":
            const activePlayer = draft.players.find((p) => p.id === draft.activePlayerId)!;
            const levelData = activePlayer.levelData.find((l) => l.Id === draft.activeLevel!.id)!;
            if (levelData.State === LevelState.NonValidMachineUsed) {
                if (CheckValidMachinesUsed(draft)) {
                    levelData.State = LevelState.InProgress;
                }
            }
            break;
    }

    return;
});


export function GetNewPlayer(name: string): PlayerModel {
    return {
        deletedMachines: [],
        id: CreateGuid(),
        levelData: [],
        machines: [],
        name,
        showAnimations: true,
        stepTime: 10,
        activeMachineId: undefined,
        activeLevel: undefined,
        cellSize: Constants.CellSize
    }
}

function CheckValidMachinesUsed(gameData: GameData): boolean {
    return GetActiveMachine(gameData)?.DependingOn.filter((mu) => IsDefaultMachine(mu)).every((mu) => gameData.activeLevel!.allowedMachines.some((am) => am === mu)) !== false;
}

function CheckLevelCompleted(draft: Draft<GameData>) {
    const activePlayer = draft.players.find((p) => p.id === draft.activePlayerId)!;
    const levelData = activePlayer.levelData.find((l) => l.Id === draft.activeLevel!.id)!;

    const onlyAllowedMachinesUsed = CheckValidMachinesUsed(draft);
    if (!onlyAllowedMachinesUsed) {
        levelData.State = LevelState.NonValidMachineUsed;
    } else if (levelData.State !== LevelState.Completed) {
        levelData.State = LevelState.InProgress;
    }

    levelData.LastUsedMachineId = activePlayer.activeMachineId;
}
